@import url("../../../fss/commons/css/common.css");
@import url("../../../fss/fss/css/default.css");

@import "base/variables"; //환경 설정
@import "../../fss/scss/breakpoint/breakpoint"; //반응형 플러그인
@import "components/slick";
@font-face {
	font-family: "BCcardB";
	src: url("../../font/bccard/BCcardB.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}
img {
	vertical-align: top;
}
#w {
	min-width: 1920px;
	margin: 0 auto;
	width: 1920px;
	.header {
		background: url(../../img/mainc_01.png) 0 0 no-repeat;
		height: 180px;
	}
}
a{
	border: 1px solid #000;

	
}
.visual {
	position: relative;
	height: 900px;
	transition: all 1s ease;
	&.off {
		filter: blur(20px);
	}
	.nav {
		position: relative;
		z-index: 100;
		padding-top: 58px;
		width: 1400px;
		margin: 0 auto;
		a {
			transition: all 0.3s ease-out;
			display: inline-block;
			position: relative;
			// width: 136px;
			font-family: "BCcardB";
			font-size: 24px;
			line-height: 36px;
			height: 36px;
			opacity: 0.5;
			margin-right: 30px;
			color: #fff;
			// background-color: #f00;
			& + a {
				margin-left: 30px;
				// margin-left: 60px;
				&::after {
					content: "";
					height: 26px;
					border-left: 1px solid #fff;
					position: absolute;
					// top: -2px;
					left: -30px;
					bottom: 4px;
				}
			}
			&.active {
				font-size: 36px;
				opacity: 1;
				position: relative;
				&::before {
					content: "";
					width: 12px;
					height: 12px;
					background-color: #2ea3ff;
					border-radius: 12px;
					position: absolute;
					right: 0;
					top: -22px;
				}
			}
		}
	}
	.item-set {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 50;
		// border: 2px solid #f00;
		width: 100%;
		height: 900px;
		.item {
			opacity: 0;
			position: absolute;
			left: 0;
			top: 0px - 10px;
			width: 100%;
			text-align: center;
			transition: all 0.8s ease;

			&.item-0 {
				padding-top: 120px;
			}
			&.item-1 {
				padding-top: 190px;
			}
			&.item-2 {
				padding-top: 186px;
			}
			&.item-3 {
				padding-top: 158px;
			}
			&.active {
				top: 0;
				opacity: 1;
			}
		}
	}
	.bg-set {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		z-index: 1;
	}
	.ctrl {
		position: relative;
		z-index: 500;
		a {
			position: absolute;
			width: 70px;
			height: 116px;
			top: 392px;
			&.left {
				left: 0;
				background-image: url(/fss/img/v-left.png);
			}
			&.right {
				right: 0;
				background-image: url(/fss/img/v-right.png);
			}
		}
	}
	.quick {
		position: absolute;
		// width: 100%;
		width: 1400px;
		// right: 0;
		bottom: 40px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1000;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		> a {
			&.left {
			}
			&.right {
			}
		}
		> div {
			flex-basis: 1140px;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			a {
			}
		}
	}
}

.quick__item {
	> a {
		// position: absolute;
		position: relative;
	}
}
.navAll {
	opacity: 0;

	// display: none;
	background-color: rgba(3, 9, 30, 0.8);

	position: absolute;
	width: 100%;
	top: 0;
	height: 900px;

	flex-direction: row;
	justify-content: center;
	align-items: center;
	transition: all 0.8s ease-out;
	display: flex;
	&.active {
		opacity: 1;
		z-index: 5000;
	}
}

.visual-set {
	position: relative;
	overflow: hidden;
}
